.nav-container {
  background-color: rgb(245, 223, 223, 0.9);
  position: fixed;
  top: 0;
  height: 8%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-content: center;
  align-items: center;
  padding-top: auto;
  z-index: 1;
}

.nav-item {
  padding-right: 2%;
}

.nav-container a {
  text-decoration: none;
  font-size: 1.1em;
  color: rgb(0, 0, 0);
  font-weight: bolder;
}

.nav-container a:hover {
  color: rgb(142, 204, 207);
  border-bottom: 2px rgb(142, 204, 207) solid;
}

.nav-container a:focus {
  color: rgb(142, 204, 207);
}

.name-logo {
  margin-right: 72vw;
  font-weight: bolder;
  font-size: 1.1em;
}
.main {
  min-height: calc(100vh - 772px - 25px);
  width: 100%;
  padding-top: 0;
  position: relative;
}

.footer .footer-container {
  background-color: rgb(245, 223, 223);
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* bottom: 0; */
  width: 100%;
  padding-top: 1%;
  padding-bottom: 1%;
  z-index: -1;
}

.footer-container a:hover {
  text-decoration: none;
  color: rgb(142, 204, 207);
  border-bottom: 2px rgb(142, 204, 207) solid;
}

.footer-container a,
p {
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: bolder;
  color: rgb(0, 0, 0);
}

.copyright:hover {
  text-decoration: none;
}

.social a {
margin-right: 2vw;
}

header h1 {
  margin-top: 28%;
}

h1 {
  font-size: 2.3rem;
  line-height: 3.5rem;
}

.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 12% 5% 5% 5%;
}

.header-container img {
  padding-left: 3%;
}

.here-link {
  text-decoration: none;
  color: rgb(142, 204, 207);
}

.here-link:hover {
  text-decoration: none;
  color: rgb(142, 204, 207);
  border-bottom: 3px rgb(142, 204, 207) solid;
}

.header-container hr {
  width: 90px;
  height: 4px;
  background: #000;
  margin: 32px 0;
  border: 0;
}

.empty-box {
  background-color: rgb(245, 223, 223);
  width: 100%;
  height: 75vh;
  position: absolute;
  z-index: -1;
}



.hr {
  width: 90%;
  height: 2px;
  background: rgb(245, 223, 223);
  margin-left: 5%;
  margin-top: 1%;
  margin-bottom: 0;
  border: 0;
}

.experience,
.projects-container,
.about-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.about-container div,
.experience div,
.projects-container div {
  margin: 5% 5% 3% 7%;
}

.about-container h4,
.projects-container h4,
.experience h4 {
  font-weight: bold;
}

.projects p, .experience p {
  margin-left: -56vw;
  font-size: 1.2rem;
  font-weight: normal;
}

.about-container .experience {
  margin-bottom: 3%;
}

.about-container .projects {
  margin-bottom: 10%;
  margin-left: 0;
}

.profile p {
  font-size: 1.2rem;
  margin-left: 20%;
  font-weight: normal;
}

.experience a {
  text-decoration: none;
  color: rgb(142, 204, 207);
}

.experience a:hover {
  text-decoration: none;
  color: rgb(142, 204, 207);
  border-bottom: 2px rgb(142, 204, 207) solid;
  font-weight: bold;
}

.projects a {
  text-decoration: none;
  color: rgb(70, 165, 170);
}

.projects a:hover {
  text-decoration: none;
  color: rgb(142, 204, 207);
  border-bottom: 2px rgb(142, 204, 207) solid;
  font-weight: bold;
}

.hr {
  width: 90%;
  height: 2px;
  background: rgb(245, 223, 223);
  margin-left: 5%;
  margin-top: 1%;
  border: 0;
}

.card-container {
  margin-top: 6%;
}

.card-container .card {
  width: 100%;
  margin-top: 20%;
  padding-top: 2%;
  margin: 0 auto;
  border: none;
  background-color: rgb(245, 223, 223, 0.9);
  margin-bottom: 5%;
}

.card div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card a {
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
  text-decoration: none;
  color: black;
  padding-bottom: 0;
  margin-bottom: 1%;
  text-decoration: underline;
}

.card a:hover {
  text-decoration: underline;
  color: rgb(142, 204, 207); 
}

.project-img {
  border: white 3px solid;
  margin-bottom: 2%;
}

.project-title {
  margin-bottom: 2%;
}

.project-description li {
  list-style: none;
  text-align: center;
  margin-left: 21vw;
  margin-right: 23vw;
  font-weight: bold;
  font-size: 1.1rem;
  margin-bottom: 1%;
}

